import React from 'react';
import {
  Segment,
  Grid,
  Header
} from 'semantic-ui-react';
import Project from './Project';
import { withProjectService } from '../../hoc';




const Projects = ({ projectService }) => {
  const projects = projectService.getProjects();

  return (

    <Segment vertical id="projects" >

      <Header as="h3" className='projects__header'
        textAlign='center'>
        My Projects
      </Header>

      <Grid columns={3} container celled stackable>
        <Grid.Row textAlign="center">
          {
            projects.map((item) => {
              return <Project key={item.id} item={item} />
            })
          }
        </Grid.Row>
      </Grid>
    </Segment>

  );


};
export default withProjectService()(Projects);
