import React from 'react';
import {
    Button,
    Container,
    Header,
} from 'semantic-ui-react';


const HomepageHeading = ({ mobile }) => {

    let clazzHeader = 'hompage__heading-header';
    let clazzSubHeader = 'hompage__heading-sub__header';
    if (mobile) {
        clazzHeader += '-mobile';
        clazzSubHeader += '-mobile';
    };
    return (
        <Container text >
            <Header
                as='h1'
                content='Yurii Levchenko'
                inverted
                className={clazzHeader}
            />
            <Header
                as='h2'
                content='Front-End Developer'
                inverted
                className={clazzSubHeader}
            />
            <Button inverted className='projects-anchor' as='a' href='#projects' primary size='huge'>
                My projects
            </Button>
            <Button className='project-page-link' target="_blank"
              rel="noopener noreferrer"
                as='a' href='https://fyurotex.github.io/' size='huge' basic color='black'>My Portfolio Page</Button>
        </Container>
    );
};
export default HomepageHeading;
